import React from 'react';
import ReactDOM from 'react-dom';
// import { EditableArea, EditorContextHelper } from '@magnolia/react-editor';
import { EditorContextHelper } from '@magnolia/template-annotations';
import { EditablePage } from './react-editor';
import Home from './pages/Home';
import Basic from './pages/Basic';
import Text from './components/Text';
import List from './components/List';
import Item from './components/Item';

const nodeName = 'spa-home';
const config = {
  componentMappings: {
    'spa-lm:pages/Home': Home,
    'spa-lm:pages/Basic': Basic,
    'spa-lm:components/Text': Text,
    'spa-lm:components/List': List,
    'spa-lm:components/Item': Item,
  },
};

class App extends React.Component {
  state = {};

  async componentDidMount() {
    const pathname = window.location.pathname;
    let templateDefinitions;
    let header;
    const pagesRes = await fetch(
      '/magnoliaAuthor/.rest/delivery/pages/' +
        nodeName +
        pathname.replace(new RegExp('(.*' + nodeName + '|.html)', 'g'), '')
    );
    const page = await pagesRes.json();

    if (EditorContextHelper.inEditor()) {
      const templateDefinitionsRes = await fetch(
        '/magnoliaAuthor/.rest/templateDefinition/v1/' + page['mgnl:template']
      );

      templateDefinitions = await templateDefinitionsRes.json();
    }

    if (pathname !== '/magnoliaAuthor/' + nodeName + '.html') {
      const headerRes = await fetch('/magnoliaAuthor/.rest/delivery/pages/' + nodeName + '/header');

      header = await headerRes.json();
    }

    this.setState({ page, templateDefinitions, header });
  }

  render() {
    const { page, templateDefinitions, header } = this.state;

    return (
      <div>
        {/* {header && <EditableArea content={header} />} */}
        {page && config && <EditablePage content={page} config={config} templateDefinitions={templateDefinitions} />}
      </div>
    );
  }
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
